<template>
	<div>
		<!--  -->
		<el-breadcrumb separator="/" class="nav-bread">
			<el-breadcrumb-item :to="{ path: '/adminUserList' }"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		<el-card id="ban-user">
			<!--  -->
			<el-form ref="banUserRef" :model="banUserForm" :rules="banUserRules" label-width="100px">
				<el-form-item label=":" prop="username">
					<el-input type="text" placeholder="" v-model="banUserForm.username" prefix-icon="el-icon-user"/>
				</el-form-item>
				<el-form-item label=":" prop="banTime">
					<el-date-picker type="datetime" placeholder="" v-model="banUserForm.banTime" :picker-options="pickerOptions"/>
				</el-form-item>
				<el-form-item label=":" prop="banMsg">
					<el-input type="textarea" placeholder="" v-model="banUserForm.banMsg" prefix-icon="el-icon-chat-round" :rows="5"/>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="banUserAction('banUserRef')"></el-button>
					<el-button @click="resetForm('banUserRef')"></el-button>
					<el-button type="primary" @click="unBanDialogVisible = true" style="float: right;"></el-button>
				</el-form-item>
			</el-form>
		</el-card>
		
		<!--  -->
		<el-dialog title="" :visible.sync="unBanDialogVisible" width="40%" @closed="resetForm('unBanUserRef')">
			<el-form ref="unBanUserRef" :model="unBanUserForm" :rules="unBanUserRules" label-width="80px">
					<el-form-item label="" prop="username">
						<el-input type="text" placeholder="" v-model="unBanUserForm.username" prefix-icon="el-icon-user" ></el-input>
					</el-form-item>
				</el-form>
			<span slot="footer">
				<el-button @click="unBanDialogVisible=false"> </el-button>
				<el-button type="primary" @click="unBanUserAction('unBanUserRef')"> </el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import { banUserByName, unBanUserByName } from '../../api/admin'
	import { responseStatus } from '../../utils/responseStatusUtil'
	export default {
		name: 'banUser',
		data(){
			var validateBanTime = (rule, value, callback) => {
			        if (value == '') {
			          callback(new Error(''));
			        } else {
			          if (value.getTime() < Date.now()) {
			           callback(new Error(''));
			          }
			          callback();
			        }
			      };
			return {
				//
				banUserForm: {
					username: '',
					banTime: 0,
					banMsg: ''
				},
				//
				unBanUserForm: {
					username: '',
				},
				//
				banUserRules: {
					username: [{required: true, message:"", trigger:'blur'}],
					banTime: [{required: true, validator: validateBanTime, trigger:'blur'}],
					banMsg: [{required: true, message:"", trigger:'blur'}]
				},
				//
				unBanUserRules: {
					username: [{required: true, message:"", trigger:'blur'}],
				},
				//
				pickerOptions: {
				disabledDate(time) {
				return time.getTime() < Date.now() - 24 * 60 * 60 * 1000 || time.getTime() > Date.now() + 5 * 365 * 24 * 60 * 60 * 1000;
				}
				},
				unBanDialogVisible: false,
			}
		},
		methods: {
			/**
			 * 
			 * @param {Object} formName
			 */
			banUserAction(formName){
				this.$refs[formName].validate(valid => {
					const data = {
						username: this.banUserForm.username,
						banTime: parseInt(this.banUserForm.banTime.getTime()/1000 - Date.now()/1000),
						banMsg: this.banUserForm.banMsg
					}
					if(valid){
						banUserByName(data).then(res => {
							if(responseStatus(res)){
								return this.$message.success("");
							}
						})
					}
				})
			},
			/**
			 * 
			 * @param {Object} formName
			 */
			unBanUserAction(formName){
				this.$refs[formName].validate(valid => {
					const data = {
						username: this.unBanUserForm.username
					}
					if(valid){
						unBanUserByName(data).then(res => {
							if(responseStatus(res)){
								return this.$message.success("");
							}
						})
					}
				})
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			}
		},
		created() {
			this.banUserForm.banTime = new Date();
		}
	}
</script>

<style scoped="scoped">
	#ban-user {
		padding-top: 30px;
	}
	.nav-bread {
		margin-bottom: 10px;
	}
	.el-card {
		max-width: 800px;
	}
</style>
