import axios from 'axios'
import qs from 'qs'

export const getUserList = (data)=>{
	return axios.get('/searchUserByString',{params: data});
}

export const banUserByName = (data)=>{
	return axios.post('/admin/BanByName',qs.stringify(data));
}

export const unBanUserByName = (data)=>{
	return axios.post('/admin/unBanByName',qs.stringify(data));
}

/**
 * 
 */
export const addCategory = (data)=>{
	return axios.post('/admin/addCategory',data);
}

/**
 * 
 */
export const getCategory = (data)=>{
	return axios.get('/getCategory');
}

/**
 * 
 */
export const getTrashList = (data)=>{
	return axios.get('/admin/getPost',{params: data});
}

/**
 * 
 */
export const rollBackPost = (data)=>{
	return axios.post('/admin/rollBackPost', qs.stringify({postId: data},{arrayFormat: 'repeat'}));
}

/**
 * 
 */
export const getPostList = (data)=>{
	return axios.get('/getPost',{params: data});
}

/**
 * 
 */
export const addEssences = (data)=>{
	return axios.post('/admin/addEssences',qs.stringify(data));
}

